import { Component, OnInit, DoCheck, ViewChild } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
import { TaskToAuthorizationService } from './task-to-authorization.service';
import { handleResult, handleError, formatDate, showMessage } from '../../common/func/common-func';
import { AuthorizationModel } from '../process-definition-model';
import { paginatorUtils, CALENDAR_CN, FULL_HORIZONTAL_DATE_FORMAT } from '../../common/constant/config';
import { EngTypeModel } from '../../process-instance/process-instance-model';
import { MessageService } from 'primeng/components/common/messageservice';
import { VALID_FLAGS, VALID_FLAGS_DESCP } from '../../common/constant/code.value';
import { AddTaskAuthorizationComponent } from './add-task-authorization/add-task-authorization.component';
import { HttpService } from 'app/@uisftech/common/service/http.service';

@Component({
  selector: 'app-task-to-authorization',
  templateUrl: './task-to-authorization.component.html',
  styleUrls: ['./task-to-authorization.component.scss']
})
export class TaskToAuthorizationComponent implements OnInit, DoCheck {
  msgs = [];
  // 
  paginatorInstance: AuthorizationModel = new AuthorizationModel();
  // 
  total: string;
  // 
  authorizationList: AuthorizationModel[] = [];
  // 
  selectedAuthorization: AuthorizationModel[] = [];
  // 
  queryAuthorization: AuthorizationModel = new AuthorizationModel();
  // 
  valiFlags: EngTypeModel[] = [];
  // 
  queryAuthorEffect: EngTypeModel = new EngTypeModel();
  // 
  modifyOrAddValiFlag: EngTypeModel = new EngTypeModel();
  // 
  authorizationType: EngTypeModel[] = [];
  // 
  modifyOrAddAuthorizationType: EngTypeModel;
  // 
  CALENDAR_CN = CALENDAR_CN;
  // 
  queryAuthorizationStartTime: Date;
  // 
  queryAuthorizationEndTime: Date;
  // 
  loginSys: string;
  // 
  currentTime: Date;
  // 
  dialogParam = {
    dialogHeight: 0,
    headerTitle: '',
    display: false,
    showModel: 0
  };
  // 
  minDate: Date = new Date('1970/1/1');
  // 
  maxDate: Date = new Date('30000/12/31');
  @ViewChild('addAuthorization') addTaskAuthorizationComponent: AddTaskAuthorizationComponent;
  // 
  valiFlagOPtions = VALID_FLAGS_DESCP;
  constructor(
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private taskToAuthorizationService: TaskToAuthorizationService,
    public httpService: HttpService
  ) {
    this.valiFlags = VALID_FLAGS;

    this.authorizationType = [
      { type: '0', descp: '' },
      { type: '1', descp: '' }
    ];
  }

  ngOnInit() {
    // userId
    this.loginSys = this.httpService.handleParams({}).commonHeader;
    this.paginatorInstance.rowNum = paginatorUtils.rowNum;
    this.paginatorInstance.startRow = paginatorUtils.startRow;
  }

  ngDoCheck(): void {   //DoCheck
    this.currentTime = new Date();
  }

  queryFirst(table) {
    table.first = 0;
    this.query(table);
  }


  // 
  query(table): void {
    table.first = table.first || 0;
    table.rows = table.rows || parseInt(paginatorUtils.rowNum, 10);
    // 
    this.paginatorInstance.rowNum = table.rows + '';
    // 
    this.paginatorInstance.startRow = (table.first / table.rows) + 1 + '';
    this.getAuthorizationList(this.queryAuthorEffect.type, this.queryAuthorization.reciveId, this.queryAuthorizationStartTime, this.queryAuthorizationEndTime, this.queryAuthorization.flowCode);
  }

  // 
  reset(dt): void {
    this.queryAuthorization.reciveId = null;
    this.queryAuthorization.flowCode = null;
    this.queryAuthorization.flowName = null;
    this.queryAuthorEffect = new EngTypeModel();
    this.queryAuthorizationStartTime = null;
    this.queryAuthorizationEndTime = null;
    this.queryFirst(dt);
  }

  // 
  getAuthorizationList(state?: string, reciveId?: string, queryAuthorizationStartTime?: any, queryAuthorizationEndTime?: any, flowCode?: string): void {
    const startTime = queryAuthorizationStartTime && formatDate(queryAuthorizationStartTime, FULL_HORIZONTAL_DATE_FORMAT);
    const endTime = queryAuthorizationEndTime && formatDate(queryAuthorizationEndTime, FULL_HORIZONTAL_DATE_FORMAT);
    if (startTime && endTime) {
      if (new Date(startTime).getTime() > new Date(endTime).getTime()) {
        showMessage(this.messageService, 'error', '');
        return;
      }
    }
    const bodyParams = {
      sendId: this.loginSys['userId'],
      state,
      reciveId,
      startTime,
      endTime,
      flowCode,
      pageRows: parseInt(this.paginatorInstance.rowNum, 10),
      curPage: parseInt(this.paginatorInstance.startRow, 10)
    };
    this.taskToAuthorizationService.queryAuthorizationList(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.authorizationList = data.pageData[0].result;
            this.total = data.pageData[0].totalRows;
            if (this.authorizationList.length > 0) {
              this.authorizationList.map(item => {
                item.isShowCancel = item.startTime < this.currentTime.getTime() ? true : false;
                item.flowName = item.flowName? item.flowName: '';
              })
            }
          }, true, { errorMsg: '' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
      );
  }

  // 
  addAuthorization(): void {
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '';
    this.dialogParam.showModel = 1;
    this.addTaskAuthorizationComponent && this.addTaskAuthorizationComponent.resetAuthorization();
  }

  // 
  cancelRequest(bodyParams, table) {
    this.taskToAuthorizationService.cancelAuthorize(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.query(table);
          }, true, { message: '', errorMsg: '' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
      );
  }
  // 
  cancleAuthorization(item, table): void {
    if (item.state === '2') {
      showMessage(this.messageService, 'error', '');
      return;
    }
    const bodyParams = {
      id: item.id + ''
    };
    this.cancelRequest(bodyParams, table);
  }

  // 
  deleteRequest(params, table) {
    this.taskToAuthorizationService.deleteAcceditRecord(params)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.query(table);
          }, true, { message: '', errorMsg: '' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
      );
  }

  // 
  deleteAuthorization(item, table): void {
    this.confirmationService.confirm({
      message: '',
      header: '',
      icon: 'fa fa-question-circle',
      accept: () => {
        const bodyParams = {
          id: item.id + ''
        };
        this.deleteRequest(bodyParams, table);
      }
    });
  }

  // 
  batchDeleteAuthorization(table) {
    if (this.selectedAuthorization.length) {
      let flag = false;
      const ids = [];
      this.selectedAuthorization.map(item=>{
        if (item.state !== '0') {
          return flag = true;
        }
        ids.push(item.id);
      });
      if (flag) {
        return showMessage(this.messageService, 'error', '');
      }
      this.confirmationService.confirm({
        message: '',
        header: '',
        icon: 'fa fa-question-circle',
        accept: () => {
          const bodyParams = {
            id: ids.join(',')
          };
          this.deleteRequest(bodyParams, table);
        }
      });
    } else {
      showMessage(this.messageService, 'error', '');
      return;
    }
  }

  // 
  batchCancelAuthorization(table) {
    if (this.selectedAuthorization.length) {
      let flag = false;
      const ids = [];
      this.selectedAuthorization.map(item=>{
        if (item.state !== '1') {
          return flag = true;
        }
        ids.push(item.id);
      });
      if (flag) {
        return showMessage(this.messageService, 'error', '');
      }
      this.confirmationService.confirm({
        message: '',
        header: '',
        icon: 'fa fa-question-circle',
        accept: () => {
          const bodyParams = {
            id: ids.join(',')
          };
          this.cancelRequest(bodyParams, table);
        }
      });
    } else {
      return showMessage(this.messageService, 'error', '');
    }
  }

  // 
  selectProcess() {
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '';
    this.dialogParam.showModel = 2;
  }
  orgid: any;
  // 
  selectSendId() {
    let sys = this.httpService.handleParams({}).commonHeader;
    if (sys.extParam['orgId']) {
      this.orgid = sys.extParam['orgId'];
    }
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '';
    this.dialogParam.showModel = 3;
  }

  // 
  selectProcessCallback(value) {
    if (Array.isArray(value)) {
      const names = [];
      const ids = [];
      value.forEach(item => {
        names.push(item.defName);
        ids.push(item.defId);
      });
      this.queryAuthorization.flowName = names.join(',');
      this.queryAuthorization.flowCode = ids.join(',');
    }
    this.onDialogHidden();
  }
  // 
  selectRoleCallback(value) {
    this.onDialogHidden();
    // 
    if (value.tellerId) {
      this.queryAuthorization.reciveId = value.tellerId;
      this.queryAuthorization.reciveName = value.tellerName;
    }
  }

  // 
  addSuccessCallback(event, table) {
    if (event) {
      this.onDialogHidden();
      this.queryFirst(table);
    }
  }

  // 
  onDialogHidden() {
    this.dialogParam.display = false;
    this.dialogParam.headerTitle = '';
    this.dialogParam.showModel = 0;
  }
}
