import { Component, OnInit } from '@angular/core';
import { TreeNode, ConfirmationService, MenuItem } from 'primeng/primeng';
import { expandLastNode, handleError, handleResult, collectAllSystemIds, showMessage } from '../../common/func/common-func';
import { ProcessMaintenanceService } from './process-maintenance.service';
import {
  ProcessDefinitionModel, BusinessNodeProsExtendModel, DictionaryTypeModel, ProjectModel, RadioConfig
} from '../process-definition-model';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Pager, CALENDAR_CN } from '../../common/constant/config';
import { checkFlowCode } from '../../common/constant/validator.utils';
import { MessageService } from 'primeng/components/common/messageservice';
import { UPDATE_VERSION_TYPE } from '../../common/constant/code.value';

@Component({
  selector: 'app-process-maintenance',
  templateUrl: './process-maintenance.component.html',
  styleUrls: ['./process-maintenance.component.scss']
})
export class ProcessMaintenanceComponent implements OnInit {
  msgs = [];
  // 
  pager: Pager = new Pager();
  // 
  processDirectorys: TreeNode[];
  // 
  expandNodes: TreeNode[];
  // 
  selectedProcessDirectory: TreeNode;
  // 
  queryProcessDefinitions: ProcessDefinitionModel[] = [];
  // 
  selectedProcessDefinition: ProcessDefinitionModel = new ProcessDefinitionModel();
  // 
  queryProcessDefinition: ProcessDefinitionModel = new ProcessDefinitionModel();
  // 
  items: MenuItem[];
  // 
  processItems: MenuItem[];
  // 
  addProcessForm: FormGroup;
  // 
  processDisplay = false;
  // 
  isModifyProcess = false;
  // 
  isAddProcess = false;
  // 
  modifyOrAddProcess: ProcessDefinitionModel = new ProcessDefinitionModel();
  // 
  defaultProcess: ProcessDefinitionModel = new ProcessDefinitionModel();

  systemName: string;

  systemId: string;

  defaultSystemId: string;

  defaultSystemName: string;
  // 
  processDirectoryDisplay = false;
  // 
  isAddProcessDirectory = false;
  // 
  isModifyProcessDirectory = false;
  // 
  processDirectoryForm: FormGroup;
  // 
  headerTip: string = null;
  // 
  total: string;
  // 
  processProsExtendDisplay = false;
  // 
  prosExtendTypes: DictionaryTypeModel[] = [];
  // 
  selectedProsExtendType: DictionaryTypeModel = new DictionaryTypeModel();
  // 
  businessNodeProsExtendList: BusinessNodeProsExtendModel[] = [];
  // 
  selectedBusinessNodeProsExtend: BusinessNodeProsExtendModel;
  // 
  addProcessNodeProsExtendPlay = false;
  // 
  addProcessNodeExtendForm: FormGroup;
  // 
  isSkipFirstNode: boolean = false;
  // 
  defaultSkipFirstNode: boolean = false;

  // 
  CALENDAR_CN = CALENDAR_CN;
  // 
  startTime: Date;
  // 
  defaultTimeStart: Date;
  //
  maxProcessHandleTime: number;
  // 
  defaultMaxProcessHandleTime: number;
  // 
  modifyProcessNodeProsExtendPlay = false;
  // 
  modifyProcessNodeExtendForm: FormGroup;
  // 
  modifyBusinessNodeProsExtend: BusinessNodeProsExtendModel = new BusinessNodeProsExtendModel();
  // 
  defaultBusinessNodeProsExtend: BusinessNodeProsExtendModel = new BusinessNodeProsExtendModel();
  // 
  projectsDisplay: boolean = false;
  // 
  projectes: ProjectModel[] = [];
  // 
  selectedProjectMenu: ProjectModel = new ProjectModel();
  // /
  userSelected: string;
  // /
  defaultUserSelected: string;
  // 
  allSystemIds: string[] = [];
  // 
  processDefineAlldefIds: string[] = [];
  // 
  copyProcessDisplay = false;
  // 
  copyProcessForm: FormGroup;
  //  
  copyProcessDefinition: ProcessDefinitionModel = new ProcessDefinitionModel();
  // 
  processVersionDisplay = false;
  // 
  importDisplay: boolean;
  uploadedFiles: any[] = [];
  // 
  selectedValues: any[] = [];
  // 
  isHistoryRest: number = 0;
  // 
  viewDisplay: Boolean = false;
  // id
  ids: any;
  // 
  publishDialogParam = {
    display: false,
    updateVersion: false   // 
  }
  // 
  updateVersionConfig = new RadioConfig();
  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private processMaintenanceService: ProcessMaintenanceService
  ) {
    this.createValidators();

  }

  ngOnInit() {
    this.queryProcessDirectoryTree(true);

    this.processItems = [
      {
        label: '', icon: 'fa fa-fw fa-plus', command: (event) => {
          this.addProcessDirectory();
        }
      },
      {
        label: '', icon: 'fa ui-icon-edit', command: (event) => {
          this.modifyProcessDirectory();
        }
      },
      {
        label: '', icon: 'fa ui-icon-delete', command: (event) => {
          this.deleteProcessDirectory();
        }
      },
      { separator: true },
      {
        label: '', icon: 'fa ui-icon-edit', command: (event) => {
          this.expandRecursive(this.selectedProcessDirectory, true);
        }
      },
      {
        label: '', icon: 'fa ui-icon-edit', command: (event) => {
          this.expandRecursive(this.selectedProcessDirectory, false);
        }
      }
    ];
    this.items = [
      {
        label: '', icon: 'fa-search', command: (event) => {
          this.toActivitiPage(this.selectedProcessDefinition.id);
        }
      },
      {
        label: '', icon: 'fa-search', command: (event) => {
          this.openPublishDialog();
        }
      },
      // { separator: true},
    ];
    this.updateVersionConfig.name= "updateVersion";
    this.updateVersionConfig.options = UPDATE_VERSION_TYPE;
  }

  createValidators() {
    this.addProcessForm = this.fb.group({
      // 'waysId': ['', [Validators.maxLength(8), checkSpecialCharacter, checkSpace]]checkFlowCode
      'storeNameName': ['', [Validators.required, checkFlowCode, Validators.maxLength(50)]],
      'nameName': ['', [Validators.required]],
      'displayName': ['']
    });

    this.processDirectoryForm = this.fb.group({
      'sysNodeName': ['', [Validators.required]],
      'sysNodeIdName': ['', [Validators.required]]
    });


    // 
    this.addProcessNodeExtendForm = this.fb.group({
      'addProsExtendTypeName': ['', [Validators.required]],
      'skipFistNodeName': ['', [Validators.required]],
      'timeToStartName': ['', [Validators.required]],
      'maxTimeName': ['', [Validators.required]],
      'addConditionlName': ['', [Validators.required]],
    });
    // 
    this.modifyProcessNodeExtendForm = this.fb.group({
      'businessNodeIdName': ['', [Validators.required]],
      'propNameName': ['', [Validators.required]],
      'skipFistNodeName': ['', [Validators.required]],
      'timeToStartName': ['', [Validators.required]],
      'maxTimeName': ['', [Validators.required]],
      'addConditionlName': ['', [Validators.required]]
    });

    this.copyProcessForm = this.fb.group({
      'storeNameName': ['', [Validators.required, checkFlowCode, Validators.maxLength(50)]],
      'nameName': ['', [Validators.required]],
      'displayName': ['']
    });

  }


  // 
  queryProcessDirectoryTree(flag: boolean, expandNodes?): void {
    const bodyParams = { id: 1 };
    this.processMaintenanceService.searchProcessDirectory(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.processDirectorys = data.pageData[0].result;
            if (this.processDirectorys.length) {
              const systemIdArray = [];
              this.allSystemIds = Array.from(collectAllSystemIds(this.processDirectorys[0], 'systemId', systemIdArray));
            }
            if (flag) {
              if (this.processDirectorys && this.processDirectorys.length) {
                this.expandRecursive(this.processDirectorys[0], true);
              }
            }
            if (expandNodes && expandNodes.length) {
              expandLastNode(this.processDirectorys, expandNodes);
            }
          }, true, { errorMsg: '' });
        },
        err => {
          handleError(err, this.messageService, () => {

          });
        });
  }



  // 
  paginate(event) {
    this.pager.first = event.first;
    this.pager.rows = event.rows;
    this.pager.page = event.page + 1;
    this.getProcessDefinitions();
  }

  // 
  queryProcess(): void {
    this.pager.first = '0';
    this.pager.page = '1';
    this.getProcessDefinitions();
  }



  // 
  getProcessDefinitions(): void {
    if (!this.selectedProcessDirectory) {
      showMessage(this.messageService, 'error', '');
      return;
    }
    const bodyParams = {
      defId: this.queryProcessDefinition.defId,
      defName: this.queryProcessDefinition.defName,
      parentId: this.selectedProcessDirectory['keyId'],
      defVersion: 0,
      pageRows: parseInt(this.pager.rows, 10),
      curPage: parseInt(this.pager.page, 10)
    };
    this.processDefineAlldefIds = [];
    this.processMaintenanceService.queryProcessDefinitionsByStore(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.queryProcessDefinitions = data.pageData[0].result;
            this.pager.totalRecords = data.pageData[0].totalRows;
            if (this.queryProcessDefinitions.length) {
              this.queryProcessDefinitions.map(item => {
                this.processDefineAlldefIds.push(item.defId);
              });
            }
          }, true, { errorMsg: '' });
        },
        err => {
          handleError(err, this.messageService, () => {

          });
        }
      );
  }

  // 
  resetQuery(): void {
    this.queryProcessDefinition.defId = null;
    this.queryProcessDefinition.defName = null;
    this.queryProcess();
  }

  // 
  onNodeSelect(event, table): void {
    const clickNodeData = event.node;
    this.headerTip = clickNodeData['systemName'];
    this.selectedProcessDefinition = null;
    this.getProcessDefinitions();
  }


  // 
  importProcess(uploadFile): void {
    uploadFile.files = [];
    if (!this.selectedProcessDirectory) {
      showMessage(this.messageService, 'error', '');
      return;
    }
    this.importDisplay = true;

  }

  myUploader(event) {
    const formData = new FormData();
    formData.append('file', event.files[0]);
    formData.append('parentId', this.selectedProcessDirectory['keyId'])
    this.processMaintenanceService.importWorkFlow(formData)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.getProcessDefinitions();
            this.selectedProcessDefinition = null;
          }, true, { message: ' ', errorMsg: '' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
      );
    this.importDisplay = false;
  }

  // 
  exportProcess(): void {
    let idsArray = [];
    let ids: any;
    let ValueArray = Array.from(new Set(this.selectedValues));
    if (ValueArray) {
      if (ValueArray.length < 1) {
        showMessage(this.messageService, 'error', '');
        return;
      } else {
        ValueArray.map(item => {
          idsArray.push(item.id);
        })
        ids = idsArray.join(',')
      }
    }
    this.processMaintenanceService.exportWorkFlow(ids);
    this.selectedProcessDefinition = null;
    this.selectedValues = [];
  }

  // 
  addProcess(): void {
    if (!this.selectedProcessDirectory) {
      showMessage(this.messageService, 'error', '');
      return;
    }
    this.reseAddProcess();
    this.processDisplay = true;
    this.isAddProcess = true;
    this.isModifyProcess = false;
  }

  // 
  reseAddProcess(): void {
    this.modifyOrAddProcess.defId = null;
    this.modifyOrAddProcess.defName = null;
    this.modifyOrAddProcess.defDesc = null;
  }

  // 
  saveProcessDefine(): void {
    if (this.processDefineAlldefIds.indexOf(this.modifyOrAddProcess.defId) > -1) {
      showMessage(this.messageService, 'error', `${this.modifyOrAddProcess.defId}`);
      return;
    }
    const bodyParams = {
      defId: this.modifyOrAddProcess.defId,
      defName: this.modifyOrAddProcess.defName,
      defDesc: this.modifyOrAddProcess.defDesc,
      parentId: this.selectedProcessDirectory['keyId']
    };
    this.processMaintenanceService.addProcessDefinition(bodyParams)
      .subscribe(
        data => {
          this.processDisplay = false;
          this.isAddProcess = false;
          this.isModifyProcess = false;
          handleResult(data, this.messageService, () => {
            this.getProcessDefinitions();
            this.selectedProcessDefinition = null;
          }, true, { message: ' ', errorMsg: '' });
        },
        err => {
          handleError(err, this.messageService, () => {

          });
        }
      );
  }


  // 
  copyWorkFlowFunc(item): void {
    this.resetCopyProcessDefine();
    this.copyProcessDefinition.id = item.id;
    this.copyProcessDisplay = true;
  }

  // 
  resetCopyProcessDefine(): void {
    this.copyProcessDefinition.defId = null;
    this.copyProcessDefinition.defName = null;
    this.copyProcessDefinition.defDesc = null;
  }

  // 
  saveCopyProcessDefine(): void {
    const bodyParams = {
      defId: this.copyProcessDefinition.defId,
      defName: this.copyProcessDefinition.defName,
      defDesc: this.copyProcessDefinition.defDesc,
      id: this.copyProcessDefinition.id
    };
    this.processMaintenanceService.copyWorkFlow(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.getProcessDefinitions();
            this.selectedProcessDefinition = null;
          }, true, { message: ' ', errorMsg: '' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
      );
    this.copyProcessDisplay = false;
  }




  // 
  modifyProcess(item): void {
    this.modifyOrAddProcess = { ...item };
    this.defaultProcess = { ...item };
    this.processDisplay = true;
    this.isModifyProcess = true;
    this.isAddProcess = false;

  }
  // 
  resetModify(): void {
    this.modifyOrAddProcess = { ...this.defaultProcess };
  }

  // 
  updateProcessDefine(): void {
    const bodyParams = {
      id: this.modifyOrAddProcess.id,
      defId: this.modifyOrAddProcess.defId,
      defName: this.modifyOrAddProcess.defName,
      defDesc: this.modifyOrAddProcess.defDesc
    };
    this.processMaintenanceService.editProcessDefinition(bodyParams)
      .subscribe(
        data => {
          this.processDisplay = false;
          this.isAddProcess = false;
          this.isModifyProcess = false;

          handleResult(data, this.messageService, () => {
            this.getProcessDefinitions();
            this.selectedProcessDefinition = null;
          }, true, { message: ' ', errorMsg: '' });
        },
        err => {
          handleError(err, this.messageService, () => {

          });
        }
      );
  }

  // 
  updateOrSaveProcessDefine(): void {
    this.isModifyProcess && this.updateProcessDefine();
    this.isAddProcess && this.saveProcessDefine();


  }
  // 
  resetProcessDefine(): void {
    this.isModifyProcess && this.resetModify();
    this.isAddProcess && this.reseAddProcess();
  }


  // 
  deleteProcess(item): void {
    if (item.publicFlag === '1') {
      showMessage(this.messageService, 'error', `${item.defId}`);
      return;
    }
    this.confirmationService.confirm({
      message: '',
      header: '',
      icon: 'fa fa-question-circle',
      key: 'mapConfirm',
      accept: () => {
        const bodyParams = {
          id: item.id,
        };
        this.processMaintenanceService.deleteProcessDefinition(bodyParams)
          .subscribe(
            data => {

              handleResult(data, this.messageService, () => {
                this.getProcessDefinitions();
                this.selectedProcessDefinition = null;
              }, true, { message: '', errorMsg: '!' });
            },
            err => {
              handleError(err, this.messageService, () => {

              });
            }
          );
      }
    });
  }
  // 
  openPublishDialog() {
    this.publishDialogParam.display = true;
    this.publishDialogParam.updateVersion = false;
  }
  // 
  publishPro(): void {
    if (!this.selectedProcessDefinition) {
      return;
    }
    const bodyParams = { id: this.selectedProcessDefinition.id, updateVersion: JSON.stringify(this.publishDialogParam.updateVersion)  };
    this.processMaintenanceService.publishProcess(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.getProcessDefinitions();
            this.cancelPublish();
          }, true, { message: '', errorMsg: '' });
        },
        err => {
          handleError(err, this.messageService, () => {

          });
        }
      );
  }
  // 
  checkWorkflow() {
    const bodyParams = { id: this.selectedProcessDefinition.id };
    this.processMaintenanceService.checkWorkFlow(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            if (data.pageData[0].errorCode && data.pageData[0].errorCode==='F') {
              // this.confirmationService.confirm({
              //   message: data.pageData[0].errorMsg + ',',
              //   header: '',
              //   icon: 'fa fa-question-circle',
              //   key: 'mapConfirm',
              //   accept: () => {
              //     this.publishPro();
              //   }
              // });
              showMessage(this.messageService, 'error', data.pageData[0].errorMsg+ ',');
              return;
            } else {
              this.publishPro();
            }
          }, true, { errorMsg: '' });
        },
        err => {
          handleError(err, this.messageService, () => {

          });
        }
      );
  }
  // 
  confirmUpdateVersion() {
    // 
    if (this.publishDialogParam.updateVersion) {
      this.checkWorkflow();
    } else {
      this.publishPro();
    }
  }
  // 
  expandRecursive(node: TreeNode, isExpand: boolean) {
    node.expanded = isExpand;
    node.children && node.children.map(childNode => {
      this.expandRecursive(childNode, isExpand);
    });
  }

  // 
  modifyProcessDirectory(): void {
    this.systemName = this.selectedProcessDirectory['systemName'];
    this.systemId = this.selectedProcessDirectory['systemId'];
    this.defaultSystemName = this.selectedProcessDirectory['systemName'];
    this.defaultSystemId = this.selectedProcessDirectory['systemId'];
    this.processDirectoryDisplay = true;
    this.isModifyProcessDirectory = true;
    this.isAddProcessDirectory = false;
  }

  // 
  updateModifyProcessDirectory(): void {
    this.expandNodes = [...this.processDirectorys];
    const bodyParams = {
      systemId: this.systemId,
      systemName: this.systemName,
      id: this.selectedProcessDirectory['keyId'],
      parentId: this.selectedProcessDirectory['parentKeyId']
    };
    this.processMaintenanceService.updateProcessDirectory(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.queryProcessDirectoryTree(false, this.expandNodes);
            this.selectedProcessDirectory = null;
          }, true, { message: ' ', errorMsg: '' });
        },
        err => {
          handleError(err, this.messageService, () => {

          });
        }
      );
    this.processDirectoryDisplay = false;
    this.isModifyProcessDirectory = false;
    this.isAddProcessDirectory = false;
  }

  // 
  resetModifyProcessDirectory(): void {
    this.systemName = this.defaultSystemName;
    this.systemId = this.defaultSystemId;
  }

  // 
  addProcessDirectory(): void {
    this.resetAddProcessDirectory();
    this.processDirectoryDisplay = true;
    this.isModifyProcessDirectory = false;
    this.isAddProcessDirectory = true;
  }

  // 
  saveAddProcessDirectory(): void {
    if (this.allSystemIds.indexOf(this.systemId) > -1) {
      showMessage(this.messageService, 'error', `${this.systemId}`);
      return;
    }
    this.expandNodes = [...this.processDirectorys];
    const bodyParams = {
      systemId: this.systemId,
      systemName: this.systemName,
      parentId: this.selectedProcessDirectory['keyId']
    };
    this.processMaintenanceService.addProcessDirectory(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.queryProcessDirectoryTree(false, this.expandNodes);
            this.selectedProcessDirectory = null;
          }, true, { message: ' ', errorMsg: '' });
        },
        err => {
          handleError(err, this.messageService, () => {

          });
        }
      );
    this.processDirectoryDisplay = false;
    this.isModifyProcessDirectory = false;
    this.isAddProcessDirectory = false;
  }

  // 
  resetAddProcessDirectory(): void {
    this.systemName = null;
    this.systemId = null;
  }


  // 
  updateOrSaveProcessDirectory(): void {
    this.isModifyProcessDirectory && this.updateModifyProcessDirectory();
    this.isAddProcessDirectory && this.saveAddProcessDirectory();
  }

  // 
  resetProcessProcessDirectory(): void {
    this.isModifyProcessDirectory && this.resetModifyProcessDirectory();
    this.isAddProcessDirectory && this.resetAddProcessDirectory();
  }
  // 
  deleteProcessDirectory(): void {
    if (Array.isArray(this.selectedProcessDirectory.children) && this.selectedProcessDirectory.children.length > 0) {
      showMessage(this.messageService, 'error', `${this.selectedProcessDirectory['systemName']} `);
      return;
    }
    this.confirmationService.confirm({
      message: '',
      header: '',
      icon: 'fa fa-question-circle',
      key: 'mapConfirm',
      accept: () => {
        this.expandNodes = [...this.processDirectorys];
        const bodyParams = {
          id: this.selectedProcessDirectory['keyId']
        };
        this.processMaintenanceService.deleteProcessDirectory(bodyParams)
          .subscribe(
            data => {

              handleResult(data, this.messageService, () => {
                this.queryProcessDirectoryTree(false, this.expandNodes);
                this.selectedProcessDirectory = null;
                this.headerTip = null;
                this.queryProcessDefinitions = [];
              }, true, { message: ' ', errorMsg: '' });
            },
            err => {
              handleError(err, this.messageService, () => {

              });
            }
          );
      }
    });
  }


  // ()
  toActivitiPage(id: number) {
    const parms = `${id}/userId001/sysCode001/tokenCode001`;
    this.processMaintenanceService.toActivitiPage(parms);
  }


  // 
  viewFlowVersion(item): void {
    this.selectedProcessDefinition = { ...item };
    this.processVersionDisplay = true;
    this.isHistoryRest++;
  }

  // 
  viewBusinessNodePropExtends(item, propsList): void {
    this.selectedProcessDefinition = { ...item };
    propsList.getBusinessNodePropExtends(item.defId);
    this.processProsExtendDisplay = true;
  }


  // 
  historyDefversionCallBack(event): void {
    this.processVersionDisplay = !event;
    this.getProcessDefinitions();
  }
  // 
  viewProcess(item) {
    this.viewDisplay = true;
    this.ids = item.id + '';
  }
  // 
  cancelPublish() {
    this.publishDialogParam.display = false;
    this.selectedProcessDefinition = null;
    this.selectedValues = [];
  }
}


