import { Injectable } from '@angular/core';
import { HttpService } from '../../common/service/http.service';

import { EXCEPTION_STRATEGY_API } from '../process-definition.constant';
import { RTS_HTTP_URL } from 'app/@uisftech/common/service/http.constant';

@Injectable()
export class ExceptionStrategyService {

  constructor(
    private httpService: HttpService
  ) {
  }

  // 
  searchSelectList = (params) => this.httpService.post(RTS_HTTP_URL + EXCEPTION_STRATEGY_API.selectList, params, null);

  // 
  updateExceptionDo = (params) => this.httpService.post(RTS_HTTP_URL + EXCEPTION_STRATEGY_API.updateExceptionDo, params, null);

  // 
  addExceptionDo = (params) => this.httpService.post(RTS_HTTP_URL + EXCEPTION_STRATEGY_API.addExceptionDo, params, null);

  // 
  deleteExceptionDo = (params) => this.httpService.post(RTS_HTTP_URL + EXCEPTION_STRATEGY_API.deleteExceptionDo, params, null);

}
