import { Injectable } from '@angular/core';
import { HttpService } from '../../common/service/http.service';
// import { RTS_HTTP_URL } from '../../common/constant/config';
import { PROCESS_MAINTENANCE_API } from '../process-definition.constant';
import { RTS_HTTP_URL } from 'app/@uisftech/common/service/http.constant';

@Injectable()
export class GeneralParametersService {

  constructor(
    private httpService: HttpService
  ) { }

  // 
  listWfParam = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.listWfParam, params, null);

  // 
  deletleWfParam = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.deletleWfParam, params, null);

  // 
  addWfParam = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.addWfParam, params, null);

  // 
  updateWfParam = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.updateWfParam, params, null);
  
  //  
  order = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.order, params, null);

  // 
  listParamType = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.listParamType, params, null);

  // /
  addUpdateWfParamTimeout = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.addUpdateWfParamTimeout, params, null);
}
