import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Pager } from '../../../../common/constant/config';
import { ProcessMaintenanceService } from '../../../process-maintenance/process-maintenance.service';
import { handleResult, handleError } from '../../../../common/func/common-func';
import { ConfirmationService } from 'primeng/primeng';
import { ProcessDefinitionModel } from '../../../process-definition-model';
import { MessageService } from 'primeng/components/common/messageservice';

@Component({
  selector: 'app-flow-template',
  templateUrl: './flow-template.component.html',
  styleUrls: ['./flow-template.component.scss']
})
export class FlowTemplateComponent implements OnInit {


  @Output() outValue: EventEmitter<any> = new EventEmitter();

  msg = [];
  // 
  pager: Pager = new Pager();

  // 
  queryProcessDefinitions: ProcessDefinitionModel[] = [];
  // 
  selectedProcessDefinition: ProcessDefinitionModel = new ProcessDefinitionModel();
  //
  queryEventService: ProcessDefinitionModel = new ProcessDefinitionModel();
  // 
  selectedValues: any[];
  // 
  paginateSelectedValues: any[];

  constructor(
    private messageService: MessageService,
    private processMaintenanceService: ProcessMaintenanceService,
    private confirmationService: ConfirmationService,
  ) { }

  ngOnInit() {
    this.getProcessDefinitions();
  }

  resetSelected(): void {
    this.selectedValues = [];
    this.selectedProcessDefinition = null;
    this.paginateSelectedValues = [];
  }


  reset(): void {
    this.queryEventService.defId = null;
    this.queryEventService.defName = null;
  }

  queryFirst(): void {
    this.getProcessDefinitions();
  }


  // 
  paginate(event) {
    this.pager.first = event.first;
    this.pager.rows = event.rows;
    this.pager.page = event.page + 1;
    this.getProcessDefinitions();
  }


  // 
  getProcessDefinitions(): void {
    const bodyParams = {
      defId: this.queryEventService.defId,
      defName: this.queryEventService.defName,
      defVersion: 0,
      pageRows: parseInt(this.pager.rows, 10),
      curPage: parseInt(this.pager.page, 10)
    };
    this.processMaintenanceService.queryProcessDefinitionsByStore(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.queryProcessDefinitions = data.pageData[0].result;
          this.pager.totalRecords = data.pageData[0].totalRows;
        });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );
  }


  selceteProcess(event): void {
    this.outValue.emit(event);
  }

}
