import { Component, OnInit, ViewChild } from '@angular/core';
import {
  DictionaryTypeModel, MessageModel
} from '../process-definition-model';
import { URLSearchParams } from '@angular/http';
import { Pager, paginatorUtils, CALENDAR_CN, FULL_HORIZONTAL_DATE_FORMAT, HORIZONTAL_LINE_SHORT_DATE_FORMAT } from '../../common/constant/config';
import { MessageService } from 'primeng/components/common/messageservice';
import { Message, TreeNode, ConfirmationService, MenuItem, DataTable } from 'primeng/primeng';
import { findObject, handleError, formatDate, handleResult, showMessage } from '../../common/func/common-func';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { EngTypeModel } from '../../process-instance/process-instance-model';
import { MessageTemplateService } from './message-template.service';
import { MESSAGE_TEMPLATE_TYPE, MESSAGE_TEMPLATE_TYPE2 } from '../../common/constant/code.value';


@Component({
  selector: 'app-message-template',
  templateUrl: './message-template.component.html',
  styleUrls: ['./message-template.component.scss']
})
export class MessageTemplateComponent implements OnInit {
  msg = [];
  // 
  messageTemplateList: MessageModel[] = [];
  // 
  paginatorInstance: MessageModel = new MessageModel();
  // 
  queryMessageTemplate: MessageModel = new MessageModel();
  // 
  selectedMessageTemplate: MessageModel = new MessageModel();
  // 
  messageTypes = MESSAGE_TEMPLATE_TYPE2;
  // 
  queryMessageType: EngTypeModel = new EngTypeModel();
  // 
  modifyOrAddMessageType: EngTypeModel = new EngTypeModel();
  // 
  defaultMessageType: EngTypeModel = new EngTypeModel();
  // 
  total: string;
  // 
  messageTemplateDisplay = false;
  // 
  isAddMessageTemplateDisplay = false;
  // 
  isModifyMessageTemplateDisplay = false;

  messageTemplateForm: FormGroup;
  //  
  modifyOrAddMessageTemplate: MessageModel = new MessageModel();
  // 
  defaultMessageTemplate: MessageModel = new MessageModel();

  @ViewChild('dt') dt: DataTable;

  CALENDAR_CN = CALENDAR_CN;
  // 
  queryCreateTime: Date;


  constructor(private fb: FormBuilder,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private messageTemplateService: MessageTemplateService) {
    this.createValidators();
  }

  ngOnInit() {
    this.paginatorInstance.rowNum = paginatorUtils.rowNum;
    this.paginatorInstance.startRow = paginatorUtils.startRow;

  }

  createValidators() {
    this.messageTemplateForm = this.fb.group({
      'templateIdName': ['', Validators.required],
      'nameName': ['', Validators.required],
      'messageTypeName': ['', Validators.required],
      'contentName': ['', Validators.required],
      'tempDescName': ['']
    });
  }


  getMessageTemplateList(templateId?: string, name?: string, type?: string, customCreateTime?: any): void {
    const createTime = customCreateTime == null ? null : formatDate(customCreateTime, HORIZONTAL_LINE_SHORT_DATE_FORMAT);
    const bodyParams = {
      templateId,
      name,
      type,
      createTime,
      pageRows: parseInt(this.paginatorInstance.rowNum, 10),
      curPage: parseInt(this.paginatorInstance.startRow, 10)
    };
    this.messageTemplateService.searchMsgTemList(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.messageTemplateList = data.pageData[0].result;
          this.total = data.pageData[0].totalRows;
        }, true, { errorMsg: '' });
      },
      err => {
        handleError(err, this.messageService, () => {

        });
      }
      );
  }


  queryFirst(table) {
    table.first = 0;
    this.query(table);
  }


  // 
  query(table): void {
    table.first = table.first || 0;
    table.rows = table.rows || parseInt(paginatorUtils.rowNum, 10);
    // 
    this.paginatorInstance.rowNum = table.rows + '';
    // 
    this.paginatorInstance.startRow = (table.first / table.rows) + 1 + '';
    this.getMessageTemplateList(this.queryMessageTemplate.templateId, this.queryMessageTemplate.name, this.queryMessageType.type, this.queryCreateTime);
  }

  // 
  reset(): void {
    this.queryMessageTemplate.templateId = null;
    this.queryMessageTemplate.name = null;
    this.queryMessageType = new EngTypeModel();
    this.queryCreateTime = null;
  }


  // 
  resetMessageTemplate(): void {
    this.isAddMessageTemplateDisplay && this.resetAdd();
    this.isModifyMessageTemplateDisplay && this.resetModify();


  }

  // 
  addMessageTemplate(): void {
    this.resetAdd();
    this.isAddMessageTemplateDisplay = true;
    this.isModifyMessageTemplateDisplay = false;
    this.messageTemplateDisplay = true;
  }


  // 
  resetAdd(): void {
    this.modifyOrAddMessageTemplate.templateId = null;
    this.modifyOrAddMessageTemplate.name = null;
    this.modifyOrAddMessageType = findObject('sms', this.messageTypes, 'type');
    this.modifyOrAddMessageTemplate.content = null;
    this.modifyOrAddMessageTemplate.tempDesc = null;
  }

  // 
  modifyMessageTemplate(item): void {
    this.modifyOrAddMessageTemplate = { ...item };
    this.defaultMessageTemplate = { ...item };
    this.modifyOrAddMessageType = findObject(this.modifyOrAddMessageTemplate.type, this.messageTypes, 'type');
    this.messageTemplateDisplay = true;
    this.isAddMessageTemplateDisplay = false;
    this.isModifyMessageTemplateDisplay = true;
  }

  // 
  resetModify(): void {
    this.modifyOrAddMessageTemplate = { ...this.defaultMessageTemplate };
    this.modifyOrAddMessageType = findObject(this.defaultMessageTemplate.type, this.messageTypes, 'type');
  }

  // 
  updateOrSaveMessageTemplate(table): void {
    if (this.isModifyMessageTemplateDisplay) {
      const modigfyBodyParams = {
        keyId: this.modifyOrAddMessageTemplate.id || '',
        templateId: this.modifyOrAddMessageTemplate.templateId || '',
        name: this.modifyOrAddMessageTemplate.name || '',
        type: this.modifyOrAddMessageType ? this.modifyOrAddMessageType.type : '',
        content: this.modifyOrAddMessageTemplate.content || '',
        tempDesc: this.modifyOrAddMessageTemplate.tempDesc || '',
      };
      this.messageTemplateService.updateMsgTem(modigfyBodyParams)
        .subscribe(
        data => {
          this.isAddMessageTemplateDisplay = false;
          this.isModifyMessageTemplateDisplay = false;
          this.messageTemplateDisplay = false;
          handleResult(data, this.messageService, () => {
            this.query(table);
          }, true, { message: '', errorMsg: '' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
        );
    } else if (this.isAddMessageTemplateDisplay) {
      const addBodyParams = {
        templateId: this.modifyOrAddMessageTemplate.templateId || '',
        name: this.modifyOrAddMessageTemplate.name || '',
        type: this.modifyOrAddMessageType ? this.modifyOrAddMessageType.type : '',
        content: this.modifyOrAddMessageTemplate.content || '',
        tempDesc: this.modifyOrAddMessageTemplate.tempDesc || '',
      };
      this.messageTemplateService.addMsgTem(addBodyParams)
        .subscribe(
        data => {
          this.isAddMessageTemplateDisplay = false;
          this.isModifyMessageTemplateDisplay = false;
          this.messageTemplateDisplay = false;
          handleResult(data, this.messageService, () => {
            this.query(table);
          }, true, { message: '', errorMsg: '' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
        );
    }

  }

  // 
  deleteMessageTemplate(item, table): void {
    if (!item.id) {
      showMessage(this.messageService, 'error', '');
      return;
    }
    this.confirmationService.confirm({
      message: '',
      header: '',
      icon: 'fa fa-question-circle',
      accept: () => {
        const bodyParams = {
          templateId: item.templateId
        };
        this.messageTemplateService.deleteMsgTem(bodyParams)
          .subscribe(
          data => {
            handleResult(data, this.messageService, () => {
              this.query(table);
            }, true, { message: '', errorMsg: '' });
          },
          err => {
            handleError(err, this.messageService, () => {
            });
          }
          );
      }
    });

  }

}
